/* ==========================================================================
   #Search tweak begin
   ========================================================================== */

// WEBKIT
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    display: none;
}

// IE
input[type="search"]::-ms-clear {
    display: none;
}

input[type=text]::-ms-clear {
    display: none;
}

/* ==========================================================================
   #Search tweak end
   ========================================================================== */

/* ==========================================================================
   Sidebar area styles - begin
   ========================================================================== */

aside.edgtf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;
    border-left: 1px solid rgba(0,0,0,.15);
	box-sizing: border-box;
    padding-left: 25px;

    .widget {

        &:last-child {
            margin: 0 !important;
        }
	}

    @include ipad-landscape {
        border-left: none;
        padding-left: 0;
    }
}

.wpb_widgetised_column {
    position: relative;

    .widget {
	
	    &:last-child {
		    margin: 0 !important;
	    }
    }
}
/* ==========================================================================
   Sidebar area styles - end
   ========================================================================== */

/* ==========================================================================
   Default WordPress Widgets styles - begin
   ========================================================================== */

.widget {
	margin: 0 0 35px;
	
	.edgtf-widget-title {
		margin: 0 0 23px;
		
		a {
			color: inherit;
		}
	}
	
	ul {
		padding: 0;
		margin: 0;
		list-style: none;
		
		li {
			margin: 0 0 10px;
			
			&:last-child {
				margin: 0;
			}
		}
	}

    /*
    *   Specific styles
    */
    &.widget_text {

        img {
            display: block;
        }
    }

    #wp-calendar {
        width: 100%;
        text-align: center;

        caption {
            color: $default-heading-color;
            margin: 0 0 15px;
        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            color: #fff;
            background-color: $first_main_color;

            a {
                color: inherit;
            }
        }
    }

    &.widget_rss {

        .edgtf-widget-title {

            .rsswidget {

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .rss-widget-icon {
            display: inline-block;
        }

        ul {

            li {
                margin: 0 0 25px;

                .rss-date {
                    display: block;
                }
            }
        }
    }

    &.widget_search {

        form {
            @include edgtfRelativeHolderLayout();
            clear: both;
            
            @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                max-width: 70%;
            }

            > div {
                @include edgtfRelativeHolderLayout();
                border-width: 0;
                border-style: solid;
                border-color: $default-border-color;
                box-sizing: border-box;
            }
        }

        input,
        button {
            position: relative;
            display: inline-block;
            vertical-align: top;
            float: left;
            height: 52px;
            font-family: inherit;
            font-weight: 300;
            font-size: 17px;
            line-height: 32px;
            color: $default-text-color;
            background-color: transparent;
            border: 0;
            border-radius: 0;
            outline: 0;
            -webkit-appearance: none;
            box-sizing: border-box;
            @include edgtfTransition(color .2s ease-out);
        }

        input {
            width: calc(100% - 41px);
            padding: 10px 0;
	
	        @include edgtfPlaceholder {
		        color: inherit;
	        }
        }

        button {
            width: 40px;
            padding: 10px 0;
            font-size: 16px;
            color: $default-text-color;
            cursor: pointer;
            @include edgtfTransition(color .2s ease-in-out);

            &:hover {
                color: $first-main-color;
            }

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }
    }

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_comments,
    &.widget_nav_menu {

        ul {

            li {
                margin: 0;
                padding: 2px 0;

                &:last-child {
                    border: none;
                }

                ul {
                    margin: 4px 0 0;
                    padding: 0 0 0 10px;
                }
            }
        }
    }

    &.widget_recent_entries {
	
	    .edgtf-widget-title {
            margin: 0 0 25px;
        }

        ul {

            li {
                margin: 0 0 12px;

                > span {
                    display: block;
	                font-size: 14px;
                }
            }
        }
    }

    &.widget_archive,
    &.widget_categories,
    &.widget_text {

        select {
            width: 100%;
            max-width: 100%;
            height: 54px;
            padding: 0 18px;
            line-height: 54px;
            color: $default-text-color;
            background-color: transparent;
            border: 1px solid $default-border-color;
            font-family: inherit;
            font-size: inherit;
            font-style: inherit;
            font-weight: inherit;
            outline: none;
            box-sizing: border-box;
        }
    }

    &.posts_holder {

        ul {
            
            li {

                &.page_item_has_children {

                    & > a {
                        display: block;
                    }

                    & > ul.children {
                        padding-left: 10px;
                    }
                }

                &.menu-item-has-children {

                    & > a {
                        display: block;
                    }

                    & > ul.sub-menu {
                        padding-left: 10px;
                    }
                }
            }
        }
    }

    &.widget_tag_cloud {

        .edgtf-widget-title {
            margin: 0 0 25px;
        }

        a {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            font-family: inherit;
            background-color: transparent;
            font-size: 14px !important;

            &:after {
                content: '/';
                color: $default-text-color;
                margin: 0 0 0 4px;
            }

            &:last-child {

                &:after {
                    display: none;
                }
            }
        }
    }
}

.vc_wp_search {

    .widget {

        &.widget_search {

            form {

                > div {
                    background-color: #fff;
                    border: none;
                }
            }

            input {
                width: calc(100% - 66px);
                padding: 0 0 0 33px;
                font-size: 15px;
            }

            button {
                width: 66px;
                padding: 0 39px 0 9px;
            }
        }
    }
}

.edgtf-page-header,
.edgtf-top-bar,
.edgtf-sticky-header,
.edgtf-mobile-header {
    
    .widget {
        margin: 0;
        
        &.widget_pages,
        &.widget_archive,
        &.widget_categories,
        &.widget_meta,
        &.widget_recent_entries,
        &.widget_recent_comments,
        &.widget_rss,
        &.widget_calendar,
        &.widget_rss,
        &.widget_archive,
        &.widget_categories,
        &.widget_tag_cloud,
        &.widget_nav_menu,
        &.widget_search {
            display: none !important;
        }
    }
}

.edgtf-page-footer {

    .edgtf-widget-title-holder {

		.edgtf-widget-title {
			letter-spacing: .35em;
		}
	}

    .edgtf-footer-bottom-holder {

        .widget {
            font-size: 15px;
            
            &.widget_pages,
            &.widget_archive,
            &.widget_categories,
            &.widget_meta,
            &.widget_recent_entries,
            &.widget_recent_comments,
            &.widget_rss,
            &.widget_calendar,
            &.widget_rss,
            &.widget_archive,
            &.widget_categories,
            &.widget_tag_cloud,
            &.widget_nav_menu,
            &.widget_search {
                text-align: left;
                
                .edgtf-widget-title {
                    margin: 20px 0 15px;
                }
            }
        }
    }

    .widget {
	    color: #ccc;
        margin: 0 0 26px;
	
	    &:last-child {
		    margin: 0 !important;
	    }
	    
        h1, h2, h3, h4, h5, h6 {
	        color: #fff;
        }
	
	    .edgtf-widget-title {
		    color: #fff;
		    margin: 0 0 25px;
	    }
	
	    a {
		    color: #ccc;
		
		    &:hover {
			    color: #fff;
		    }
	    }
	    
        table thead tr,
        table tbody tr {
            border-color: #ccc;
        }

        table tbody th {
            border-right-color: #ccc;
        }

        /*
        *   Specific styles
        */
	    #wp-calendar {
		
		    caption {
			    color: #ccc;
		    }

            td#today {
                color: $default-heading-color;
                background-color: #ccc;
            }
	    }
	
	    &.widget_rss {
		
		    .edgtf-widget-title {
			
			    .rsswidget {
				    color: #fff;
			    }
		    }
	    }
	    
        &.widget_search {

            form {

                > div {
                    border-color: #ccc;
                }
            }

            input {
                color: #ccc;
	            
	            @include edgtfPlaceholder {
		            color: inherit;
	            }

                &[type='submit'] {
                    color: #ccc;
                }
            }

            button {

                &:hover {
                    color: #fff;
                }
            }
        }

        &.widget_archive,
        &.widget_categories,
        &.widget_text {

            select {
                color: #ccc;
                border-color: #ccc;

                option {
                    background-color: #363636;
                }
            }

        }

        &.widget_tag_cloud {

            a {
                color: #ccc;

                &:after {
                    color: inherit;
                }

                &:hover {
                    color: #fff;
                }
            }
        }
        
        &.widget_edgtf_instagram_widget {
            
            .edgtf-widget-title {
                margin-bottom: 33px;
            }
            
        }
        
    }
}

.edgtf-top-bar {

	a {
        color: #4a4a4a;

        &:hover{
            color: #fff;
        }
    }

    .widget {
        display: inline-block;
        vertical-align: middle;
        margin: 0;

        p {
            margin: 0;
        }

        &.widget_text {
            margin: 0 8px 0 0;
        }

        &.widget_nav_menu {

            ul {
                clear: both;

                li {
                    float: left;
                    padding: 0 8px;
                    margin: 0;
                    line-height: 14px;
                    border-left: 1px solid #fff;
                }
            }
        }
    }
}
/* ==========================================================================
   Default WordPress Widgets styles - end
   ========================================================================== */