@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.edgtf-comment-holder {
    @include edgtfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .edgtf-comment-holder-inner {
        padding: 123px 0 20px;
    }
	
	.edgtf-comment {
		
		&.edgtf-pingback-comment {
			
			.edgtf-comment-text {
				padding: 0;
			}
		}
	}

    .edgtf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include edgtfRelativeHolderLayout();
            margin: 15px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 108px;
        }
    }

    .edgtf-comments-title {
        margin-bottom: 38px;

        > * {
            margin: 0;
        }
    }

    .edgtf-comment-image {
        position: relative;
        display: block;
        top: 10px;
        left: 0;
        width: 82px;
        height: 82px;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .edgtf-comment-text {
        padding: 5px 0 0 108px;
        min-height: 82px;

        .edgtf-comment-info {
            @include edgtfRelativeHolderLayout();
            margin: 6px 0 0;
        }
	
	    .replay,
	    .comment-reply-link,
	    .comment-edit-link {
		    font-size: 12px;
		    font-weight: 700;
		    line-height: 2.916666666666667em;
		    letter-spacing: .2em;
		    text-transform: uppercase;
		    color: $first-main-color;
		
		    &:hover {
			    color: $default-heading-color;
		    }
	    }
	    
	    .replay,
	    .comment-edit-link {
		    float: right;
	    }

        .edgtf-comment-name {
            float: left;
            margin: 0;
        }

        .edgtf-comment-date {
	        display: none;
            float: left;
            margin: 0;
            font-size: 12px;
        }

        .edgtf-text-holder p {
            margin: 6px 0 3px;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }
	
	    ul, ol {
		    
		    li {
			    display: list-item;
		    }
	    }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.edgtf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;
    
    .edgtf-comment-form-inner {
	    font-size: 15px;
	    padding: 94px 0 78px;
	    
	    input[type='email'] {
		    float: right;
	    }

	    #respond textarea {
		    margin: 14px 0 20px 0;
	    }
    }
    
    .comment-reply-title {
        margin: 0 0 24px;
    }
    
    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .edgtf-comment-holder {
	    
        .edgtf-comment-list {
	        
            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {
	
    .edgtf-comment-holder {
	    
        .edgtf-comment-list {
	        
            .children {
                padding: 0;
            }
        }

        .edgtf-comment-text {
	        
            .edgtf-comment-date,
            .edgtf-comment-name {
                float: none;
            }
	        
            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {
	
    .edgtf-comment-holder {

        .edgtf-comment-image {
            float: none;
        }

        .edgtf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.edgtf-style-form textarea,
.wpcf7-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
	@extend %input-style;
}

.post-password-form input[type='password'] {
	display: block;
	max-width: 570px;
	margin-top: 10px;
}

.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;
		
		footer & {
			margin: 0;
		}
	}
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
	@include edgtfButtonDefaultStyle();
	@include edgtfButtonMediumParams();
	@include edgtfButtonOutlineColor();
	cursor: pointer;
	border-radius: 0;
	-webkit-border-radius:0;
	-webkit-appearance: none;
	
	&:hover {
		@include edgtfButtonOutlineHoverColor();
	}
}

p.form-submit,
.wpcf7-form p {
	margin: 0;
}

#respond textarea,
.edgtf-style-form textarea,
.wpcf7-form textarea {
	display: block;
}

.edgtf-two-columns-form-without-space {
	
	.wpcf7 & {
		border: 1px solid $default-border-color;
		@include edgtfTransition(border-color .3s ease-out);
		
		.touch & {
			cursor: pointer;
		}
		
		&:hover {
			border-color: $default-heading-color;
		}
	}
	
	.wpcf7-form .ajax-loader {
		display: none;
	}
	
	.wpcf7-form-control.wpcf7-text,
	.edgtf-btn.submit {
		height: 55px;
		background-color: transparent;
		border: 0;
	}
	
	.wpcf7-form-control.wpcf7-text {
		margin-bottom: 0;
		border-right-color: transparent;
	}
	
	.edgtf-btn.submit {
		padding: 0 12px;
		font-size: 18px;
		line-height: 1;
		left: -1px;
		color: $default-heading-color;
		border-left-color: transparent;
	}
}

.edgtf-page-footer {
	
	.wpcf7 .edgtf-two-columns-form-without-space {
		border-color: rgba(#fff, .41);
		width: calc(100% - 15px);
		
		&:hover {
			border-color: #fff;
		}
	}
	
	.edgtf-two-columns-form-without-space {
		
		.wpcf7-form-control.wpcf7-text,
		.edgtf-btn.submit {
			height: 45px;
			
		}
		
		.wpcf7-form-control.wpcf7-text {
			color: #b1b2b2;
			padding: 17px 23px;
			
			&:focus {
				color: #fff;
			}
		}
		
		.edgtf-btn.submit {
			color: #fff;
			line-height: 2.8em;
		}
	}
}

.edgtf-custom-link-style {
	
	a {
		color: $default-text-color;
		
		&:hover {
			color: $default-heading-color;
		}
	}
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.edgtf-owl-slider {
	visibility: hidden;
	
	.edgtf-light-skin.edgtf-owl-custom-nav & {
		
		.owl-nav {
			.owl-prev {
				cursor: url(../../assets/img/light-navigation-left.png), auto;
			}
			.owl-next {
				cursor: url(../../assets/img/light-navigation-right.png), auto;
			}
		}
	}
	
	.edgtf-owl-custom-nav & {
		
		.owl-nav {
			
			.owl-prev,
			.owl-next {
				position: absolute;
				content: '';
				top: 50%;
				width: 22%;
				height: 100%;
				border: none;
				@include edgtfTransform(translateY(-50%));
				
				> span {
					display: none;
				}
			}
			
			.owl-prev {
				left: 0;
				cursor: url(../../assets/img/dark-navigation-left.png), auto;
			}
			.owl-next {
				right: 0;
				cursor: url(../../assets/img/dark-navigation-right.png), auto;
			}
		}
	}
	
    .owl-stage-outer {
        z-index: 1;
    }
	
	&.edgtf-slider-is-moving {
		
		.owl-item {
			
			.edgtf-block-drag-link {
				z-index: -1 !important;
			}
		}
	}
    
    &.edgtf-slider-has-both-nav {
        
        .owl-nav {
            
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include edgtfTransform(translateY($pos));
            }
        }
    }

    &.edgtf-slider-hide-pagination .owl-dots  {
        display: none;
    }
	
	.owl-nav {
		
		.edgtf-nav-light-skin & {
			.owl-prev,
			.owl-next {
				> span {
					svg {
						stroke: #fff;
					}
				}
			}
		}
		
		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			width: 13px;
			height: 25px;
			text-align: center;
			color: $default-heading-color;
			z-index: 3;
			@include edgtfTransform(translateY(-50%));
			
			> span {
				svg {
					fill: transparent;
					stroke: $first-main-color;
				}
			}
		}
		
		.owl-prev {
			left: 24px;
		}
		
		.owl-next {
			right: 24px;
		}
	}
	
	.owl-dots {
		text-align: center;
		margin: 16px 0 0;
		
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			
			span {
				display: inline-block;
				vertical-align: middle;
				width: 12px;
				height: 12px;
				margin: 0 6px;
				border-radius: 3em;
				background-color: transparent;
				border: 2px solid rgba($default-heading-color, .5);
				box-sizing: border-box;
				@include edgtfTransition(background-color .2s ease-out, border .2s ease-out);
			}
			
			&:hover,
			&.active {
				
				span {
					background-color: $default-heading-color;
					border-color: $default-heading-color;
				}
			}
		}
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}


    + .edgtf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .edgtf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;
                @include edgtfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.edgtf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .edgtf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }


    }

}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {

    .edgtf-wrapper {
        padding: 0 !important;
    }

    .edgtf-content-inner {
	    @include edgtfTableLayout();
    }

    .edgtf-page-not-found {
	    @include edgtfTableCellLayout();
        padding: 10% 28% 10%;
	    text-align: center;

	    @include ipad-landscape {
		    padding: 160px 8% 70px;
	    }

        .edgtf-404-title {
            margin: 0;
	        font-size: 120px;
	        line-height: 1.07em;
        }
	
	    .edgtf-404-subtitle {
            margin: 0;
        }

	    .edgtf-404-tagline {
            margin: 0;
		    font-size: 12px;
		    font-weight: 700;
		    line-height: 2.8em;
		    text-transform: uppercase;
		    letter-spacing: .35em;
		    color: #777777;
        }
	    
	    .edgtf-404-text {
		    margin: 19px 0 0;
		    letter-spacing: -.01em;
		    color: #777777;
	    }

        .edgtf-btn {
            margin: 34px 0 0;
	
	        &.edgtf-btn-light-style {
		        color: #585858;
		        background-color: #fff;
		        border-color: #fff;
		
		        &:hover {
			        color: #fff !important;
			        background-color: transparent !important;
			        border-color: #fff !important;
		        }
	        }
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#edgtf-back-to-top {
    position: fixed;
	right: 25px;
	bottom: 25px;
	width: 56px;
	height: 28px;
    margin: 0;
	font-size: 13px;
	line-height: 27px;
	text-align: center;
	color: #545454;
	z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;
        @include edgtfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include edgtfTransition(opacity 0.3s ease);
    }

    span {
        position: relative;
        display: block;
	    width: 100%;
	    height: 100%;
	    padding-top: 3px;
	    border-bottom: 2px solid currentColor;
	    box-sizing: border-box;
	    @include edgtfTransform(translateY(0));
	    @include edgtfTransitionTransform(.4s ease);
	    
	    &:hover {
		
		    @include edgtfTransform(translateY(-5px));
		    
		    &:after {
			    right: auto;
			    left: 3px;
			    width: calc(100% - 5px);
		    }
	    }
	    
	    &:before {
		    content: '';
		    position: absolute;
		    top: 6px;
		    left: 8px;
		    width: 39px;
		    height: 39px;
		    border-top: 2px solid currentColor;
		    border-left: 2px solid currentColor;
		    @include edgtfTransform(rotate(45deg));
	    }
	    
	    &:after {
		    position: absolute;
		    content: '';
		    right: 0;
		    bottom: 0;
		    height: 2px;
		    width: 0;
		    background-color: currentColor;
		    @include edgtfTransition(width .4s ease);
	    }
    }
}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.edgtf-self-hosted-video-holder {
	
    .edgtf-video-wrap {
	
	    .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}
/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */

/* ==========================================================================
   #Audio Page styles - begin
   ========================================================================== */

.edgtf-audio-page-player-holder {
	position: fixed;
	bottom: 20px;
	left: 20px;
	width: 50px;
	height: 45px;
	z-index: 9999;
	
	.edgtf-audio-control,
	.edgtf-audio-pause {
		@include edgtfAbsoluteHolderLayout();
	}
	
	.edgtf-audio-control {
		padding: 10px;
		background-color: $default-heading-color;
		box-sizing: border-box;
	}
	
	.edgtf-audio-pause {
		left: 10px;
	}
	
	.edgtf-audio-play,
	.edgtf-audio-pause {
		fill: none;
		stroke: #fff;
		width: 30px;
	}
	
	&.edgtf-playing {
		
		.edgtf-audio-play {
			opacity: 0;
		}
		
		.edgtf-audio-pause {
			opacity: 1;
		}
	}
	
	&.edgtf-pause {
		
		.edgtf-audio-play {
			opacity: 1;
		}
		
		.edgtf-audio-pause {
			opacity: 0;
		}
	}
}

/* ==========================================================================
   #Audio Page styles - end
   ========================================================================== */